___CSS_LOADER_EXPORT___.push([module.id, `.reservationsSection {
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  overflow-y: scroll;
  background-color: rgb(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 5rem 0;
  font-family: 'Montserrat', sans-serif;
}

.reservationsContainer {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border: 0.4rem solid #000;
  padding: 3.8rem;
  width: 46rem;
  margin: auto;
  color: rgb(46, 46, 46);
  font-size: 0.8rem;
}

.close-icon {
  text-align: right;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 500;
}

.movieImage {
  margin-bottom: 2rem;
}

.headings {
  margin-bottom: 0.8rem;
  padding: 1.5rem 0;
  font-size: 1.8rem;
}

.movieTitle {
  font-size: 2rem;
  font-weight: 700;
  color: #5800ff;
}

.sectionContainers {
  padding: 1rem;
  margin-top: 3rem;
}

.formFields {
  display: block;
  width: 80%;
  height: 3rem;
  border-radius: 1rem;
  margin-bottom: 1rem;
  border: 1.4px solid rgba(35, 31, 32, 0.124);
  padding-left: 1rem;
  font-size: 1rem;
}

.formFields:focus {
  border: none;
  outline: none;
}

.submitBtn {
  display: flex;
  background-color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.5rem;
  color: #5800ff;
  box-shadow: 1px 1px 3px 1px rgb(0, 0, 0, 0.3);
}

.reservation-list-item {
  font-size: 1.4rem;
  font-weight: 500;
}

.star-reservation {
  display: block;
  margin-bottom: 2rem;
  padding-left: 1rem;
}

.giveReservationText {
  padding-bottom: 0.6rem;
}

.star-reservation input {
  display: none;
}

label {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-image: url('https://dl.dropboxusercontent.com/s/1oh3h6o20k4285x/star-empty.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  box-shadow: 1px 1px 3px 1px rgb(0, 0, 0, 0.3);
}

.star-reservation input:checked + label {
  background-image: url('https://dl.dropboxusercontent.com/s/oij2qr7r2ulfi4i/star-filled.png');
}
